<div class="description">
    <p>
        <b>Communiquez et gagnez en efficacité avec Go Fast !</b>
        <br>
        Détachez vous du fonctionnement du SI et recentrez vous sur l'essentiel. Revenez à votre cœur de métier !
        <br>
        L'offre Go Fast allie les fonctionnalités Office 365 au savoir-faire Eliade, et vous permet de profiter d'une solution complète de la
        migration de votre messagerie au support technique.
    </p>
    <br>
    Comment ? Go Fast inclut :
    <ul>
        <li>Prise en compte de l'existant</li>
        <li>Activation des fonctionnalités de messagerie et de calendrier professionnel d'Office 365</li>
        <li>Migration de vos boites mails dans le nouvel environnement</li>
        <li>Accès à vos fichiers depuis n'importe où grâce à votre espace de stockage OneDrive en ligne</li>
        <li>Création de réunion en ligne et d'espaces d'échanges avec vos clients, fournisseurs et collègues avec Teams</li>
        <li>Mise à disposition de guide d'utilisation et tutoriels vidéos</li>
        <li>Accès à notre support niveau 3</li>
        <li>Cout à l'utilisateur et au mois</li>
    </ul>
    
    <img src="images/MicrosoftTeams-image.png" alt="O365" class="image">
